//    
const level_table = {
  F: {
    levels: [
      "F1-I",
      "F1-",
      "F2-I",
      "F2-",
      "F2-",
      "F3-I",
      "F3-",
      "F3-",
      "F4-I",
      "F4-",
      "F4-",
      "F5",
    ],
    experience: {
      "F1-I": 0,
      "F1-": 13500,
      "F2-I": 27000,
      "F2-": 39000,
      "F2-": 51000,
      "F3-I": 63000,
      "F3-": 78000,
      "F3-": 93000,
      "F4-I": 108000,
      "F4-": 126000,
      "F4-": 144000,
      F5: 162000,
    },
  },
  B: {
    levels: ["B1", "B2", "B3", "B4", "B5", "B6"],
    experience: {
      B1: 0,
      B2: 24000,
      B3: 52000,
      B4: 78000,
      B5: 117000,
      B6: 169000,
    },
  },
  G: {
    levels: ["G1", "G2", "G3", "G4", "G5", "G6"],
    experience: {
      G1: 0,
      G2: 24000,
      G3: 52000,
      G4: 78000,
      G5: 117000,
      G6: 169000,
    },
  },
  T: {
    levels: ["T1", "T2", "T3", "T4", "T5", "T6"],
    experience: {},
  },
};

//   
// return  ,   ,  , %
export const getTotalExpInfo = (level, totalExp) => {
  if (!level) {
    return {
      nextLevel: "None",
      nextLevelExp: 0,
      remainExp: 0,
      percent: 0,
    };
  }

  const { levels, experience } = level_table[level.slice(0, 1)];
  const currentIndex = levels.indexOf(level);

  //   
  if (currentIndex === levels.length - 1) {
    return {
      nextLevel: "max",
      nextLevelExp: experience[level],
      remainExp: 0,
      percent: 100,
    };
  } else if (currentIndex === -1) {
    //   
    return {
      nextLevel: "None",
      nextLevelExp: 0,
      remainExp: 0,
      percent: 0,
    };
  }

  const nextLevel = levels[currentIndex + 1];
  const nextLevelExp = experience[nextLevel];
  const remainExp = Math.max(nextLevelExp - totalExp, 0); //  
  const percent = Math.round((totalExp / nextLevelExp) * 100);
  return {
    nextLevel,
    nextLevelExp,
    remainExp,
    percent,
  };
};
